@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  color: rgb(18, 18, 18);
}

body {
  background-color: rgb(255, 255, 255);
}
body main section {
  display: flex;
  margin-top: 96px;
  margin-left: 230px;
}
body main section img {
  width: 505px;
  height: 330px;
}
body main section article {
  width: 380px;
  margin-left: 95px;
}
body main section article header {
  font-weight: 700;
  font-size: 42px;
  height: 50px;
  margin-top: 40px;
}
body main section article p {
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  margin-top: 24px;
  margin-bottom: 32px;
}
body main section article button {
  background-color: rgb(228, 193, 174);
  border: none;
  border-radius: 32px;
  font-weight: 700;
  font-size: 16px;
  color: #FFFFFF;
  width: 128px;
  height: 48px;
}